import axios from "axios";

// 
if (process.env.NODE_ENV === "development") {
  axios.defaults.baseURL = "";
} else if (process.env.NODE_ENV === "debug") {
  axios.defaults.baseURL = "";
} else if (process.env.NODE_ENV === "production") {
  axios.defaults.baseURL = "";
}
axios.defaults.timeout = 30000;
axios.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded;charset=UTF-8;multipart/form-data";
// 
axios.interceptors.request.use(
  function (config) {
    return config;
  },
  function (error) {
    return Promise.reject({ status: 633, message: error });
  }
);
// 
axios.interceptors.response.use(
  function (response) {
    return response;
  },
  function (error) {
    return Promise.resolve({ status: 633, message: error.response });
  }
);

/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params: params,
      })
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .post(url, params)
      .then((res) => {
        resolve(res.data);
      })
      .catch((err) => {
        reject(err.data);
      });
  });
}

/**
 *  arrayBuffer
 */
export function getArrayBuffer(url, params) {
  return new Promise((resolve, reject) => {
    axios
      .get(url, {
        params,
        headers: { "Content-Type": "application/json,charset=utf-8" },
        responseType: "arraybuffer", //
      })
      .then((res) => {
        resolve(res);
      })
      .catch((e) => {
        reject(e);
      });
  });
}
